/* Navbar */

.nav-bar{
    height: 80px;
    background-color: #000;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 40px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
}

.nav-bar h2{
    font-size: 40px;
}

.nav-bar a{
    text-decoration: none;
    color: #fff;
}

.nav-bag{
    display: flex;
    align-items: center;
}

.icon{
    width: 35px !important;
    height: 35px !important;
}

.bag-quantity{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    background: yellow;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    font-size: 14px;
    font-weight: 600;
    color: #000;
    top: 13px;
    left: 95.1%;
}

/* Not Found */

.not-found{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: rgba(68, 68, 68);
    min-height: 80vh;
}

.not-found h2{
    font-size: 55px;
}

.not-found p{
    font-size: 20px;
}

/* Home */

.home-container{
    padding: 2rem 4rem;
    padding-top: 6rem;
}

.home-container h2{
    font-size: 40px;
    font-weight: 500;
    text-align: center;
}

.products{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 2rem;
}

.product{
    width: 250px;
    max-width: 700px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 1rem auto;
    padding: 1rem;
    border-radius: 15px;
    cursor: pointer;
    box-shadow: -5px -5px 20px rgba(255, 255, 255, 0.5), 2px 2px 5px rgba(94, 104, 121, 0.3);
}

.products .product h3{
    font-size: 15px;
    text-align: center;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.product img{
    width: 60%;
    margin-top: 1rem;
    margin-left: auto;
    margin-right: auto;
}

.desc{
    font-size: 10px;
}

.details{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.price{
    font-size: 20px;
    font-weight: 700;
}

.btn{
    width: 100%;
    height: 40px;
    border-radius: 5px;
    margin-top: 2rem;
    font-weight: 400;
    border: none;
    outline: none;
    cursor: pointer;
    letter-spacing: 1.15px;
    background-color: #4b70e2;
    color: #fff;
}

/* Cart */

.cart-container{
    padding: 2rem; 
}

.cart-container h2{
    font-weight: 400;
    font-size: 30px;
    text-align: center;
}

.cart-container .titles{
    margin: 3rem 0 1rem 0;
}

.cart-container .titles h3{
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: left;
}

.cart-item, .titles{
    display: grid;
    align-items: center;
    grid-template-columns: 2.5fr 1fr 1fr .2fr;
    column-gap: 0.5rem;
}

.cart-item{
    border-top: 1px solid rgb(187, 187, 187);
    padding: 1rem 0;
}

.product-title{
    padding-left: 0.5rem;
}

.cart-container .titles .total{
    padding-right: 1rem;
    justify-self: right;
}

.cart-product{
    display: flex;
}

img{
    width: 100px;
    max-width: 100%;
    margin-right: 1rem;
}

.cart-product h3{
    font-weight: 500;
    font-size: 15px;
    padding-right: 2rem;
    line-height: 1.5;
}

.cart-product button{
    border: none;
    outline: none;
    margin-top: 0.7rem;
    cursor: pointer;
    background: none;
    color: gray;
    padding-top: 3rem;
}

.cart-product button:hover{
    color: #000;
}

.cart-product-quantity{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: 120px;
    border: 0.5px solid rgb(177, 177, 177);
    border-radius: 5px;
}

.cart-product-quantity button{
    border: none;
    outline: none;
    background: none;
    padding: 0.7rem 1.5rem;
    cursor: pointer;
}

.cart-product-quantity .count{
    padding: 0.5rem 0;
}

.cart-product-total-price{
    justify-content: right;
    padding-right: 0.5rem;
    font-weight: 700;
}

.cart-summary{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    border-top: 1px solid rgb(187, 187, 187);
    padding-top: 2rem;
}

.cart-summary .clear-cart{
    width: 130px;
    max-width:100%;
    height: 40px;
    border-radius: 5px;
    font-weight: 400;
    letter-spacing: 1.15px;
    border: 0.5px solid rgb(177, 177, 177);
    color: gray;
    background: none;
    outline: none;
    cursor: pointer;
}

.cart-checkout{
    width: 270px;
    max-width: 100%;
}

.cart-checkout .cart-subtotal{
    display: flex;
    justify-content: space-between;
    font-size: 20px;
}

.cart-checkout .cart-subtotal .amount{
    font-weight: 700;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.cart-checkout p{
    font-size: 14px;
    font-weight: 200;
    margin: 0.5rem 0;
}

.cart-checkout button{
    width: 100%;
    height: 40px;
    border-radius: 5px;
    font-weight: 400;
    border: none;
    outline: none;
    cursor: pointer;
    letter-spacing: 1.15px;
    background-color: #4b70e2;
    color: #fff;
}

.continue-shopping, .start-shopping{
    margin-top: 1rem;
}

.continue-shopping a, .start-shopping a{
    color: gray;
    text-decoration: none;
    display: flex;
    align-items: center;
}

.continue-shopping a span, .start-shopping a span{
    margin-left: 0.5rem;
}
.cart-empty{
    font-size: 20px;
    margin-top: 2rem;
    color: rgb(84, 84, 84);
    display: flex;
    flex-direction: column;
    align-items: center;
}